/* first two styles are for sticky navbar */
.top-bar {
  position: sticky;
  top: 0;
  width: 100%;
  background: #2b7a78;
  z-index: 1; /* set z-index: 1 so checkboxes don't overlap with top bar */
}

/* On very short screens, don't want sticky navbar */
@media screen and (max-height: 550px) {
  .top-bar {
    position: static;
  }
}

/* margin: 0 auto; and width: 90%; are to center top bar content, leaving 5% width on each side */
/* but to do that, we really just need margin-left: auto and margin-right: auto */
.list-top-bar-content {
  display: flex;
  align-items: center;
  margin: 0 auto;
  width: 90%;
  height: 60px;
}

.top-bar h2 {
  font-size: 21px;
  color: #feffff;
  margin: 0;
  width: min(300px, calc(100vw - 160px));
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.list-back-button {
  width: auto;
}

.list-icons {
  display: flex;
  align-items: center;
}
