.list {
  border-radius: 7px;
  background: #feffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  color: #172528;
  margin-bottom: 1em;
  padding: 0.4em 2em 0.4em 1.3em;
  display: grid;
  align-items: center;
  justify-content: flex-start;
  column-gap: 1em;
}

.list i {
  font-size: 25px !important;
  text-align: center;
}

.list h2 {
  font-weight: 600;
  font-size: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-left: 10px;
}

.list-icon {
  width: 35px;
  margin-left: -4px;
}

i.list-icon {
  color: #172528;
  font-size: 25px !important;
}

.list-grid {
  grid-template-columns: 1.9em 1fr 1.2em;
}

.edit-list-grid {
  grid-template-columns: 1.9em 1fr 1em 3.3em;
  box-shadow: 0 4px 8px 4px rgba(0, 0, 0, 0.2);
}

.large-screen-list-card {
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 0em;
  padding: 0em 0em 0.3em 0em;
  column-gap: 1em;
  font-size: 8pt;
  grid-template-columns: 1.9em 15em 1fr;
  width: 246px;
}

.large-screen-list-card h2 {
  font-weight: 400;
  font-size: 15px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding-left: 1em;
}


.large-screen-list-card i {
  margin-left: 0.6em;
  font-size: 19px !important;
}

i.large-screen-arrow {
  color: #feffff;
  margin-left: 1em;
}

@media screen and (min-width: 769px) and (min-height: 689px) {
  i.large-screen-arrow {
    margin-left: 0em;
  }
}

.large-screen-list-card:hover {
  background: #EAE8E8;
  margin-bottom: 0em;
  padding-left: 3em;
  padding: 0em 0em 0.3em 0em;
  column-gap: 1em;
  font-size: 8pt;
  grid-template-columns: 1.9em 15em 1fr;
}

.large-screen-list-card:hover > i {
  color:#172528;
}

.selected-list {
  background: #EAE8E8;
  margin-bottom: 0em;
  padding-left: 3em;
  padding: 0em 0em 0.3em 0em;
  column-gap: 1em;
  font-size: 8pt;
  grid-template-columns: 1.9em 15em 1fr;
}

.selected-list > i.fa-chevron-right {
  color:#EAE8E8;
}


